
import { defineComponent } from 'vue';
import { IonPage, IonHeader, IonToolbar, IonContent, IonSearchbar, IonList, IonLabel, IonItem } from '@ionic/vue';

import  {useDrugStore} from "@/stores/store.drugs";
import { DrugInterface } from '@/services/interface.drugs';

export default defineComponent({
  name: 'DrugsView',
  inject: ['axios'],
  components: { IonHeader, IonToolbar, IonContent, IonPage, IonSearchbar, IonList, IonLabel, IonItem },
  setup(){
    const drugStore = useDrugStore()
    // abc.start()
    return {drugStore}
  },
  data() {
    return {
    }
  },
  methods: {
    navToCalculate(ind) {
      this.$router.push(
        {
          name: 'drugsCalculateDose',
          params: {id: ind}
        })
    },
    filterDrugs(event) {
      let searchFilter = event.target.value.toLowerCase()
      if (searchFilter.length >= 1) {
        this.drugStore.filteredList = this.drugStore.allDrugList.filter(
          // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
          (drug: DrugInterface) => drug.GENERIC_NAME!.toLowerCase().indexOf(searchFilter) > -1
        )
      }
      else {
        this.drugStore.filteredList = this.drugStore.allDrugList
      }
    }
  }

});
