import { defineStore } from "pinia";
// import axios from "axios";
import mydate from "@/services/thuocServices";
import { DrugInterface } from "@/services/interface.drugs";

export const useDrugStore = defineStore("DrugStore", {
    //state
    state: () => (
        {
            allDrugList: [] as DrugInterface[],
            filteredList: [] as DrugInterface[],
            currentDrug: {} as DrugInterface
        }),
    //actions
    actions: {
        async start() {
            await mydate.start()
            this.allDrugList = mydate.finalDanhSachThuoc
            this.filteredList = mydate.finalDanhSachThuoc
        },
        updateCurrentDrug(id:number){
            //TODO: Mỗi lần mở trong danh sách thuốc, sẽ cập nhật lại loại thuốc mới.
            this.currentDrug = this.allDrugList[id]
            this._UpdateCurrentDrugType()
        },
        _UpdateCurrentDrugType(){
            if (this.currentDrug.PER_TIME == "dose" || this.currentDrug.PER_TIME == "single") {
                //Là loại chích nhát
                this.currentDrug.cal_typeCalculate = "chichNhat"
            } else if (this.currentDrug.PER_TIME == "day"){
                // Là loại 50mg/kg/day chia ra mỗi 6h hay 8h...
                this.currentDrug.cal_typeCalculate = "chichNhatMoiNgay"
            } else {
                // Là loại truyền liên tục
                this.currentDrug.cal_typeCalculate = "truyenLienTuc"
            }
        }
    },
    //getters
    getters: {
    }
})